/**
 * –†–∞–Ω–¥–æ–º–Ω–µ —á–∏—Å–ª–æ –≤—ñ–¥ 0 –¥–æ n
 *
 * @example getRandomInt(50) // 27
 * @param {number} num - –î–æ
 * @returns {number} –†–∞–Ω–¥–æ–º–Ω–µ —á–∏—Å–ª–æ
 */
const getRandomInt = (num) => Math.floor(Math.random() * num); // –†–∞–Ω–¥–æ–º–Ω–µ —Ü—ñ–ª–µ —á–∏—Å–ª–æ

/**
 * –†–∞–Ω–¥–æ–º–Ω–∏–π –µ–ª–µ–º–µ–Ω—Ç –∑ –º–∞—Å—Å–∏–≤—É
 *
 * @example randomChoice(['foo', 'bar', 'zoo']) // 'bar'
 * @example randomChoice('fooBarZoo') // 'o'
 * @param {array | string} array - –ú–∞—Å—Å–∏–≤
 * @returns –†–∞–Ω–¥–æ–º–Ω–∏–π –µ–ª–µ–º–µ–Ω—Ç –∑ –º–∞—Å—Å–∏–≤—É
 */
const randomChoice = (array) => array[getRandomInt(array.length)];
